<template>
  <div class="cluster-details">
    <div class="crumbs">
      <span class="crumbs-item">服务器</span>
      <router-link class="crumbs-item" :to="{name:'Cluster'}">集群</router-link>
      <span class="crumbs-item qingse-text">{{ cluster.title }}详情</span>
    </div>
    <div class="v-content">
      <div class="panel info-panel head margin-b-24">
        <div class="btns-group alter-button">
          <button class="comb-btn waves-effect lvse" @click="alter" v-bind:class="{ hide:toAlter }">修改</button>
          <button class="comb-btn waves-effect lvse" @click="submitAlter" v-bind:class="{ hide:altering }">确认</button>
          <button class="comb-btn waves-effect lvse" @click="cancelAlter" v-bind:class="{ hide:altering }">取消</button>
        </div>
        <div class="info-left">
          <div class="info-icon"><img class="vam" src="../assets/cluster-avatar.png" alt=""></div>
        </div>
        <div class="info-right article">
          <h5 class="qingse-text editable">{{ cluster.name }}</h5>
    			<div class="status"><span>状态：共 {{ cluster.used + cluster.free }} GB 内存</span></div>
    			<div class="status">
    				<span class="qingse-text">█</span>
    				<span>已使用 </span>
    				<span class="qingse-text">{{ cluster.used }} GB</span>
    			</div>
    			<div class="status">
    				<span class="zise-text">█</span>
    				<span>空闲</span>
    				<span class="zise-text">{{ cluster.free }} GB</span>
    			</div>
          <br>
    			<p class="time">更新时间：{{ cluster.update_time }}</p>
    			<p class="editable">{{ cluster.description }}</p>
        </div>
      </div>
      <div class="panel">
        <div class="btns-group">
          <router-link :to="{ name:'AddHost' }"><button type="button" class="comb-btn waves-effect lvse">添加主机</button></router-link>
          <button type="button" class="comb-btn waves-effect qingse" @click="moveMachine">迁移主机</button>
          <button type="button" class="comb-btn waves-effect hongse" @click="delMachine">删除主机</button>
          <search class="right"></search>
        </div>
        <table class="striped highlight">
          <col width="40px">
          <thead>
            <tr>
              <th><checkbox></checkbox></th>
              <th>名称</th>
              <th>IP</th>
              <th>状态</th>
              <th>地址</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="host in cluster.hosts">
              <td><checkbox></checkbox></td>
              <td>{{ host.name }}</td>
              <td>{{ host.ip }}</td>
              <td>{{ host.status }}</td>
              <td>{{ host.site }}</td>
              <td>
                <router-link :to="{ name:'Machine-Details', params: { id: host.id } }" class="lvse-text table-details_btn">详情</router-link>
                <span class="hongse-text table-details_btn" @click="delMachine">删除</span>
                <span class="qingse-text table-details_btn" @click="moveMachine">迁移</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="clearfix"><pages class="right" :allpage="5" :nowpage="1" @change="pageChange"></pages></div>
      </div>
    </div>
    <!-- 迁移主机 -->
    <modal title="迁移主机至" buttons="确定,取消" buttonsClass="comb-btn lvse,comb-btn qingse" ref="movemachine" class="comb-dialog mini">
      <tselect :data="clusters_to_move"></tselect>
    </modal>
    <!-- 删除主机 -->
    <modal buttons="确定,取消" buttonsClass="comb-btn lvse,comb-btn qingse" ref="delmachine" class="comb-dialog mini">
      <div class="comb-dialog_info center-align red-text large"><i class="ten-icon">&#xe691;</i> <span v-html="delbody"></span></div>
    </modal>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'cluster-details',
  // test data
  data () {
    return {
      clusters_to_move: [
        {label: '集群1', active: true},
        {label: '集群2'},
        {label: '集群3'},
        {label: '集群4'},
        {label: '集群5'}
      ],
      cluster: {},
      delbody: '',
      toAlter: false,
      altering: true,
      title: '',
      details: ''
    }
  },
  methods: {
    delMachine () {
      this.delbody = '您确定删除主机吗？'
      this.$refs.delmachine.show()
    },
    moveMachine () {
      this.$refs.movemachine.show()
    },
    alter () {
      this.toAlter = true
      this.altering = false
      var editable = document.getElementsByClassName('editable')
      for (var i = 0; i < editable.length; i++) {
        editable[i].contentEditable = true
        if (editable[i].tagName === 'H5') {
          this.title = editable[i].innerHTML
        }
        if (editable[i].tagName === 'P') {
          this.details = editable[i].innerHTML
        }
      }
    },
    submitAlter () {
      this.toAlter = false
      this.altering = true
      var editable = document.getElementsByClassName('editable')
      for (var i = 0; i < editable.length; i++) {
        editable[i].removeAttribute('contenteditable')
      }
    },
    cancelAlter () {
      this.toAlter = false
      this.altering = true
      var editable = document.getElementsByClassName('editable')
      for (var i = 0; i < editable.length; i++) {
        editable[i].removeAttribute('contenteditable')
        if (editable[i].tagName === 'H5') {
          editable[i].innerHTML = this.title
        }
        if (editable[i].tagName === 'P') {
          editable[i].innerHTML = this.details
        }
      }
    },
    pageChange () {
      return
    }
  },
  mounted () {
    axios.get('static/api/clusters.json').then(response => {
      var clusters = response.data.clusters
      for (var i = 0; i < clusters.length; i++) {
        if (clusters[i].id === parseInt(this.$route.params.id)) {
          this.cluster = clusters[i]
        }
      }
    })
  }
}
</script>

<style scoped lang="scss">
  @import "../scss/cluster-details";
  table {
    th:first-child, td:first-child {
      width: 16px;
    }
    th:last-child, td:last-child {
      width: 24%;
    }
  }
</style>



